<template>
  <div>
    <h1>管理员列表</h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="250px"></el-table-column>
      <el-table-column prop="username" label="用户名"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="$router.push(`/admin_users/edit/${scope.row._id}`)">编辑
          </el-button>
          <el-button type="text" size="small" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: []
    }
  },
  methods: {
    async fetchList () {
      const res = await this.$http.get('/rest/admin_users')
      this.items = res.data
    },
    // 删除分类
    async remove (row) {
      this.$confirm('是否删除管理员账号', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.$http.delete(`/rest/admin_users/${row._id}`)
          console.log(res)
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.fetchList()
        })
    }
  },
  created () {
    this.fetchList()
  }
}
</script>

